.game-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    padding-bottom: 10px;
    border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.game-header h1 {
    font-size: 2rem;
    font-weight: 900;
    text-transform: uppercase;
    color: var(--accent-color);
    text-shadow: 3px 3px 0 rgba(0, 0, 0, 0.3);
    letter-spacing: 4px;
}

.pro-badge {
    font-size: 0.8rem;
