    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 999;
}

/* Responsive Design */
@media (max-width: 1024px) {
    .game-main {
        grid-template-columns: 1fr;
        max-width: 400px;
        margin: 0 auto;
    }
    
    .game-left-panel, .game-sidebar {
        width: 100%;
    }
    
    .game-header h1 {
        font-size: 1.5rem;
    }
}

@media (max-width: 768px) {
    body {
        padding: 5px;
    }
    
    .game-container {
        padding: 10px;
    }
    
    .game-header h1 {
        font-size: 1.3rem;
        letter-spacing: 2px;
    }
    
    .pro-badge {
        font-size: 0.7rem;
        padding: 2px 8px;
    }
    
    .btn-small {
        padding: 5px 10px;
        font-size: 0.9rem;
    }
    
    #game-canvas {
        width: 250px !important;
        height: 500px !important;
    }
    
    .tutorial-grid {
        grid-template-columns: 1fr;
    }
    
    .final-stats {
        grid-template-columns: 1fr;
    }
}

/* Animations */
@keyframes comboPopup {
    0% { opacity: 0; transform: translate(-50%, -50%) scale(0.5); }
    50% { opacity: 1; transform: translate(-50%, -50%) scale(1.2); }
    100% { opacity: 0; transform: translate(-50%, -50%) scale(1); }
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideUp {
    from { transform: translateY(30px); opacity: 0; }
    to { transform: translateY(0); opacity: 1; }
}
