hB,iBAAiB;AACrB;;AAEA;IACI,oCAAoC;IACpC,gBAAgB;IAChB,kBAAkB;IAClB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA,qBAAqB;AACrB;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,oBAAoB;IACpB,YAAY;AAChB;;AAEA,sBAAsB;AACtB;IACI;QACI,0BAA0B;QAC1B,gBAAgB;QAChB,cAAc;IAClB;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,iBAAiB;IACrB;AACJ;;AAEA;IACI;QACI,YAAY;IAChB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,iBAAiB;QACjB,mBAAmB;IACvB;;IAEA;QACI,iBAAiB;QACjB,gBAAgB;IACpB;;IAEA;QACI,iBAAiB;QACjB,iBAAiB;IACrB;;IAEA;QACI,uBAAuB;QACvB,wBAAwB;IAC5B;;IAEA;QACI,0BAA0B;IAC9B;;IAEA;QACI,0BAA0B;IAC9B;AACJ;;AAEA,eAAe;AACf;IACI,KAAK,UAAU,EAAE,2CAA2C,EAAE;IAC9D,MAAM,UAAU,EAAE,2CAA2C,EAAE;IAC/D,OAAO,UAAU,EAAE,yCAAyC,EAAE;AAClE;;AAEA;IACI,OAAO,UAAU,EAAE;IACnB,KAAK,UAAU,EAAE;AACrB;;AAEA;IACI,OAAO,2BAA2B,EAAE,UAAU,EAAE;IAChD,KAAK,wBAAwB,EAAE,UAAU,EAAE;AAC/C","sourcesContent":["/* ===== TETRIS PRO - LAYOUT OPTIMIS ===== */\n\n:root {\n    --theme-bg: linear-gradient(135deg, #2c3e50, #1a1a2e);\n    --board-bg: #1a1a2e;\n    --grid-color: #34495e;\n    --text-color: #ecf0f1;\n    --accent-color: #e74c3c;\n    --glow-effect: none;\n    --pixel-effect: auto;\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n    background: var(--theme-bg);\n    color: var(--text-color);\n    min-height: 100vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    overflow: auto;\n    padding: 10px;\n}\n\n/* Container principal - Layout optimis */\n.game-container {\n    background: rgba(44, 62, 80, 0.95);\n    border-radius: 15px;\n    padding: 15px;\n    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);\n    max-width: 1200px;\n    width: 100%;\n    position: relative;\n    backdrop-filter: blur(10px);\n}\n\n/* Header compact */\n.game-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 15px;\n    padding-bottom: 10px;\n    border-bottom: 2px solid rgba(255, 255, 255, 0.1);\n}\n\n.game-header h1 {\n    font-size: 2rem;\n    font-weight: 900;\n    text-transform: uppercase;\n    color: var(--accent-color);\n    text-shadow: 3px 3px 0 rgba(0, 0, 0, 0.3);\n    letter-spacing: 4px;\n}\n\n.pro-badge {\n    font-size: 0.8rem;\n    background: linear-gradient(45deg, #f39c12, #e74c3c);\n    padding: 3px 10px;\n    border-radius: 15px;\n    box-shadow: 0 2px 10px rgba(243, 156, 18, 0.4);\n    margin-left: 10px;\n}\n\n.header-controls {\n    display: flex;\n    gap: 8px;\n}\n\n.btn-small {\n    padding: 6px 12px;\n    font-size: 1rem;\n    background: rgba(255, 255, 255, 0.1);\n    border: 2px solid rgba(255, 255, 255, 0.2);\n    color: white;\n    border-radius: 8px;\n    cursor: pointer;\n    transition: all 0.3s ease;\n}\n\n.btn-small:hover {\n    background: rgba(255, 255, 255, 0.2);\n    transform: translateY(-2px);\n}\n\n/* Layout principal - Grid optimis */\n.game-main {\n    display: grid;\n    grid-template-columns: 180px 300px 220px;\n    gap: 15px;\n    justify-content: center;\n    align-items: start;\n}\n\n/* Panneau gauche compact */\n.game-left-panel {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n}\n\n.hold-piece, .statistics-panel {\n    background: rgba(26, 26, 46, 0.6);\n    padding: 12px;\n    border-radius: 12px;\n    border: 2px solid rgba(255, 255, 255, 0.1);\n}\n\n.hold-piece h3, .statistics-panel h3 {\n    text-align: center;\n    margin-bottom: 8px;\n    color: #3498db;\n    font-size: 0.9rem;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n}\n\n.hold-piece kbd {\n    background: rgba(255, 255, 255, 0.2);\n    padding: 2px 6px;\n    border-radius: 3px;\n    font-size: 0.7rem;\n    margin-left: 3px;\n}\n\n.hold-hint {\n    text-align: center;\n    font-size: 0.7rem;\n    color: rgba(255, 255, 255, 0.5);\n    margin-top: 5px;\n}\n\n#hold-canvas {\n    display: block;\n    margin: 0 auto;\n    background: var(--board-bg);\n    border: 2px solid rgba(255, 255, 255, 0.1);\n    border-radius: 6px;\n}\n\n.stat-item {\n    display: flex;\n    justify-content: space-between;\n    padding: 6px 10px;\n    margin: 4px 0;\n    background: rgba(255, 255, 255, 0.05);\n    border-radius: 6px;\n    border-left: 3px solid #3498db;\n    font-size: 0.85rem;\n}\n\n.stat-value {\n    font-weight: bold;\n    color: #f39c12;\n}\n\n.combo-highlight {\n    color: #e74c3c;\n    text-shadow: 0 0 10px #e74c3c;\n}\n\n/* Zone de jeu */\n.game-board {\n    position: relative;\n}\n\n#game-canvas {\n    background: var(--board-bg);\n    border: 3px solid rgba(255, 255, 255, 0.2);\n    border-radius: 8px;\n    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);\n    display: block;\n}\n\n.combo-display {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    font-size: 2.5rem;\n    font-weight: 900;\n    color: #FFD700;\n    text-shadow: 0 0 15px #FFD700;\n    pointer-events: none;\n    opacity: 0;\n    white-space: nowrap;\n}\n\n/* Panneau droit compact */\n.game-sidebar {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n}\n\n.next-pieces {\n    background: rgba(26, 26, 46, 0.6);\n    padding: 12px;\n    border-radius: 12px;\n    border: 2px solid rgba(255, 255, 255, 0.1);\n}\n\n.next-pieces h3 {\n    text-align: center;\n    margin-bottom: 10px;\n    color: #9b59b6;\n    font-size: 0.9rem;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n}\n\n.next-piece-container {\n    display: flex;\n    justify-content: center;\n    margin: 8px 0;\n    padding: 8px;\n    background: rgba(255, 255, 255, 0.05);\n    border-radius: 6px;\n}\n\n.next-piece-container.small {\n    opacity: 0.7;\n    transform: scale(0.9);\n}\n\n.next-piece-container canvas {\n    background: var(--board-bg);\n    border: 2px solid rgba(255, 255, 255, 0.1);\n    border-radius: 4px;\n}\n\n.game-stats {\n    background: rgba(26, 26, 46, 0.6);\n    padding: 12px;\n    border-radius: 12px;\n    border: 2px solid rgba(255, 255, 255, 0.1);\n}\n\n.stat {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 8px;\n    margin: 6px 0;\n    background: rgba(255, 255, 255, 0.05);\n    border-radius: 6px;\n    border-left: 3px solid #f39c12;\n    font-size: 0.85rem;\n}\n\n.stat span:first-child {\n    font-weight: 600;\n    color: #bdc3c7;\n}\n\n.score-value {\n    font-size: 1.2rem !important;\n    font-weight: 900 !important;\n    color: #27ae60 !important;\n}\n\n.level-value {\n    font-size: 1.1rem !important;\n    font-weight: 900 !important;\n    color: #e74c3c !important;\n}\n\n.level-progress {\n    margin-top: 10px;\n}\n\n.progress-bar {\n    width: 100%;\n    height: 8px;\n    background: rgba(255, 255, 255, 0.1);\n    border-radius: 8px;\n    overflow: hidden;\n}\n\n.progress-fill {\n    height: 100%;\n    background: linear-gradient(90deg, #3498db, #9b59b6);\n    transition: width 0.3s ease;\n    box-shadow: 0 0 8px rgba(52, 152, 219, 0.5);\n}\n\n/* Boutons de contrle compacts */\n.game-controls {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n/* Contrle de sensibilit */\n.sensitivity-control {\n    background: rgba(26, 26, 46, 0.6);\n    padding: 12px;\n    border-radius: 12px;\n    border: 2px solid rgba(255, 255, 255, 0.1);\n    margin-top: 12px;\n}\n\n.sensitivity-control h3 {\n    text-align: center;\n    margin-bottom: 10px;\n    color: #9b59b6;\n    font-size: 0.9rem;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n}\n\n.sensitivity-slider {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n    margin-bottom: 10px;\n}\n\n.sensitivity-slider label {\n    font-size: 0.85rem;\n    color: #bdc3c7;\n    font-weight: 600;\n}\n\n.sensitivity-slider input[type=\"range\"] {\n    width: 100%;\n    height: 6px;\n    border-radius: 5px;\n    background: rgba(255, 255, 255, 0.1);\n    outline: none;\n    -webkit-appearance: none;\n}\n\n.sensitivity-slider input[type=\"range\"]::-webkit-slider-thum